<template>
  <div class="chat-card-root">
    <van-image
      :src="chat.avatar"
      width="100%"
      height="100%"
      lazy-load
      fit="cover"
    />
    <div class="chat-card-status offline" v-if="chat.chatStatus === 0">
      Offline
    </div>
    <div class="chat-card-status online" v-if="chat.chatStatus === 1">
      Online
    </div>
    <div class="chat-card-status busy" v-if="chat.chatStatus === 2">Busy</div>
    <div class="chat-card-bottom">
      <div class="info">
        <div class="name">{{ chat.nickname }}</div>
        <div class="price">
          {{ chat.preMinuteDiamond }}
          <van-icon :name="require('@/assets/icon/diamond-icon.png')" />/min
        </div>

        <div class="age">
          <van-icon
            :name="require('@/assets/icon/famale-icon.png')"
            color="#fff"
            size="12"
          />
          {{ chat.age }}
        </div>
      </div>
      <div class="video-call">
        <!-- <van-button
        class="video-button"
          :icon="require('@/assets/icon/video-open-icon.png')"
          color="#f45d82;"
          @click="connect(chat)" ></van-button> -->
        <img
          class="video-button"
          :src="require('@/assets/icon/video-button.png')"
          @click="connect(chat)"
        />
      </div>
    </div>
    <Connect
      :uid="curHost.uid"
      :poster="curHost.avatar"
      @close="connectShow = false"
      v-if="connectShow"
    />
  </div>
</template>

<script>
import Vue from "vue";
import { Image, Lazyload } from "vant";
import Connect from "../components/Connect";
import * as Server from "../server";
import { mapState, mapMutations } from "vuex";

Vue.use(Lazyload).use(Image);
export default {
  name: "chat-card",
  components: {
    Connect
  },
  data() {
    return {
      curHost: null,
      connectShow: false
    };
  },
  props: {
    chat: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  computed: {
    ...mapState(["profile"])
  },
  methods: {
    ...mapMutations(["updateProfile"]),
    async loadProfile() {
      let { data, status } = await Server.Profile();
      if (status !== 0) {
        console.log("LoadProfile Failed => ", data);
        return;
      }
      this.updateProfile(data);
      localStorage.setItem("profile", JSON.stringify(data));
    },
    async connect(info) {
      info.uid = 3301603086703212;
      console.log("Connect => ", info.uid, " => ", info);
      await this.loadProfile();
      console.log("Connect loadProfile => ", this.profile);
      if (!this.profile.diamond || this.profile.diamond < 100) {
        this.$toast("The balance is insufficient, please recharge");
        this.$router.push("/recharge");
        return;
      }
      this.curHost = info;
      this.connectShow = true;
    }
  }
};
</script>

<style lang="less">
.chat-card-root {
  width: 100%;
  height: 220px;
  border-radius: 8px;
  background-color: #ddd;
  position: relative;
  overflow: hidden;

  * {
    color: #000 im !important;
  }

  .chat-card-status {
    position: absolute;
    right: 5px;
    top: 8px;
    height: 22px;
    display: flex;
    align-items: center;
    padding: 0 8px;
    font-size: 14px;
    font-weight: 400;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 4px;

    &::before {
      content: "";
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: #ddd;
      margin-right: 5px;
    }
    &.online::before {
      background-color: #40dc29;
    }
    &.busy::before {
      background-color: #ffaa0f;
    }
  }
  .chat-card-bottom {
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    left: 0;
    height: auto;
    width: 100%;
    padding: 16px 5px;
    display: grid;
    grid-template-columns: auto 50px;
    gap: 5px;
    background: linear-gradient(
      180deg,
      rgba(167, 167, 167, 0) 0%,
      rgba(0, 0, 0, 1) 100%
    );

    .info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .name {
      font-size: 15px;
      color: #fff;
      font-weight: bold;
      line-height: 29px;
      height: 29px;
      overflow: hidden;
    }
    .age {
      background-color: #f45d82;
      font-size: 12px;
      font-weight: 300;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      height: 16px;
      padding: 0 8px;
      border-radius: 4px;
      // margin-left: 10px;
    }

    .price {
      font-size: 12px;
      font-weight: 400;
      color: #fff;
      line-height: 20px;
      display: flex;
      align-items: center;
    }

    .video-call {
      text-align: right;
      vertical-align: bottom;
    }

    .video-button {
      width: 50px;
      height: 50px;
    }
  }
}
</style>
