<template>
  <div class="chat-home-root">
    <img class="list-banner" :src="require('@/assets/banner.png')" />
    <!-- <van-nav-bar title="Title" fixed :border="false" /> -->
    <van-pull-refresh
      pulling-text="Pull down to refresh..."
      loosing-text="Release to refresh..."
      loading-text="Loading..."
      v-model="refreshing"
      @refresh="onRefresh"
    >
      <!-- <div class="more-to-personal" @click="moreToPersonal">More ></div> -->
      <van-list
        loading-text="Loading..."
        finished-text="Loading..."
        v-model="loading"
        :finished="finished"
        @load="onLoad"
        class="card-list"
      >
        <div
          class="chat-card-wrap"
          v-for="(_, i) in chats"
          :key="i"
          @click="goChatDetail(_)"
        >
          <ChatCard :chat="_" :userdiamond="profile.diamond" />
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import Vue from "vue";
import { PullRefresh, List, NavBar } from "vant";
import ChatCard from "../components/ChatCard";
import * as Server from "@/server";

Vue.use(PullRefresh)
  .use(List)
  .use(NavBar);
export default {
  components: {
    ChatCard
  },
  data() {
    return {
      page: 1,
      chats: [],
      refreshing: false,
      loading: false,
      finished: true,
      profile: {}
    };
  },
  created: function() {
    console.log("Home Created");
  },
  methods: {
    moreToPersonal() {
      this.$router.push("/recharge");
    },
    async loadData() {
      const { data, status } = await Server.HomeList(this.page);
      this.refreshing = false;
      this.loading = false;
      if (status === 0) {
        const { records, total } = data;
        if (this.page === 1) {
          this.chats = records || [];
        } else {
          this.chats = this.chats.concat(records || []);
        }
        if (this.chats.length >= total) {
          this.finished = true;
        } else {
          this.finished = false;
        }
      }
    },
    onLoad() {
      this.page = this.page + 1;
      this.loadData();
    },
    onRefresh() {
      this.page = 1;
      this.loadData();
    },
    goChatDetail(item) {
      console.log("chat detail => ", item);
      // this.$router.push(`chat-detail/${item.uid}`);
      // this.$router.push(`chat-detail/3311605876848841`);
    }
  },
  mounted() {
    this.loadData();
  }
};
</script>

<style lang="less">
.chat-home-root {
  .list-banner {
    width: 100%;
    margin-bottom: 10px;
  }
}
.more-to-personal {
  text-align: right;
  font-size: 14px;
  line-height: 20px;
  color: #262626;
}
.chat-home-root {
  padding: 0 10px;
  padding-top: 10px;
  background-clip: #fff;
  // padding-top: 56px;

  // .chat-card-wrap + .chat-card-wrap {
  //   margin-top: 20px;
  // }
}
.card-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}
</style>
